/**
 * External dependencies
 */
import debounce from 'lodash/debounce';
import type { HeatmapFrameAction } from '@nab/types';

/**
 * Internal dependencies
 */
import { domReady } from '../../../public/utils/helpers';

type MaybeHeatmapFrameAction = {
	readonly plugin?: string;
};

/**
 * Internal dependencies
 */
import { reactToParentMessages as heatmapReact } from './heatmap';
import { reactToParentMessages as scrollmapReact } from './scrollmap';
import { reactToParentMessages as confettiReact } from './confetti';

import { loadAssets } from './internal/page';
import { loadResults } from './internal/store';
import { getWordPressData } from './internal/utils';

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';

// Data Status Diagram:
//
//  a) waiting-script » script-loaded » loading » still-loading » ready
//     »» error (rationale)
//
//  b) ready » processing » ready

function init() {
	const wpdata = getWordPressData();
	if ( ! wpdata ) {
		console.error( 'Parent window doesn’t have expected API. Bye!' ); // eslint-disable-line
		return;
	} //end if

	const { dispatch } = wpdata;
	const { setPageAttribute } = dispatch( NAB_DATA );
	setPageAttribute( 'heatmap/dataStatus', { mode: 'script-loaded' } );

	const { select } = wpdata;
	const { getExperiment, getPageAttribute } = select( NAB_DATA );

	const experimentId = getPageAttribute( 'editor/activeExperiment' ) ?? 0;
	const alternativeIndex =
		getPageAttribute( 'heatmap/activeAlternative' ) || 0;
	const experiment = getExperiment( experimentId );
	if ( ! experiment ) {
		setPageAttribute( 'heatmap/dataStatus', {
			mode: 'error',
			rationale: 'experiment-not-found',
		} );
		return;
	} //end if

	const options = {
		apiUrl: select( NAB_DATA ).getPluginSetting( 'apiUrl' ),
		awsAuthToken: getPageAttribute( 'heatmap/awsAuthToken' ) ?? '',
		dispatch,
		select,
		siteId: getPageAttribute( 'heatmap/siteId' ) ?? '',
		timezone: getPageAttribute( 'heatmap/timezone' ) ?? '',
	};

	let width = document.body.clientWidth;
	window.addEventListener(
		'resize',
		debounce( () => {
			const status = getPageAttribute( 'heatmap/dataStatus' );
			if (
				'still-loading' !== status?.mode &&
				'ready' !== status?.mode
			) {
				return;
			} //end if

			const resolution = getPageAttribute( 'heatmap/activeResolution' );
			if ( ! resolution || 'desktop' !== resolution ) {
				return;
			} //end if

			const mode = getPageAttribute( 'heatmap/activeMode' );
			if ( ! mode || 'scrollmap' === mode ) {
				return;
			} //end if

			const newWidth = document.body.clientWidth;
			if ( width === newWidth ) {
				return;
			} //end if
			width = newWidth;

			setPageAttribute( 'heatmap/postMessageProxy', {
				plugin: 'nelio-ab-testing',
				type: 'recompute-click-coordinates',
				mode,
			} );
		}, 100 )
	);

	window.addEventListener( 'message', reactToParentMessages, false );
	void loadAssets();
	void loadResults( experiment, alternativeIndex, options );
} //end init()

function reactToParentMessages( message: { data: MaybeHeatmapFrameAction } ) {
	const action = message.data;
	if ( ! isHeatmapFrameAction( action ) ) {
		return;
	} //end if
	heatmapReact( action );
	scrollmapReact( action );
	confettiReact( action );
} //end reactToParentMessages()

const isHeatmapFrameAction = (
	action: MaybeHeatmapFrameAction
): action is HeatmapFrameAction => action.plugin === 'nelio-ab-testing';

// =======
// KICKOFF
// =======

domReady( init );
