/**
 * External dependencies
 */
import first from 'lodash/first';
import keyBy from 'lodash/keyBy';
import last from 'lodash/last';
import mapValues from 'lodash/mapValues';
import type {
	Experiment,
	HeatmapLabels,
	HeatmapResolution,
	Maybe,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { addQueryArgs } from '../../../../public/utils/url';
import { getElementByXPath } from '../../../../public/tracking/xpath';
import { isPageReady, onPageReady } from './page';
import {
	getHours,
	getMinMaxWidthInResolution,
	getWordPressData,
} from './utils';
import { refreshHeatmap } from '../heatmap';
import type {
	AwsPaginatedResults,
	Click,
	HeatmapResults,
	ResultLoadingOptions,
	Scroll,
	WordPressData,
} from './types';

const NO_BB: BoundingBox = { top: 0, left: 0, width: 0, height: 0 };
type BoundingBox = {
	readonly top: number;
	readonly left: number;
	readonly width: number;
	readonly height: number;
};

type AlternativeIndex = number;

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';
const PAGES_TO_REFRESH = 2;
const MIN_HOURS_TO_ESTIMATE_PROGRESS = 10;

let totalHoursRunning = 0;
let results: Maybe< HeatmapResults >;
let boundingBoxes: Record<
	HeatmapResolution,
	Maybe< Record< string, BoundingBox > >
> = {
	desktop: undefined,
	tablet: undefined,
	smartphone: undefined,
};

let shouldResultLoadingBeStopped = false;
export function stopResultLoading(): void {
	shouldResultLoadingBeStopped = true;
} //end stopResultLoading()

let didLoadResultsRun = false;
export async function loadResults(
	experiment: Experiment,
	alternative: AlternativeIndex,
	options: ResultLoadingOptions
): Promise< void > {
	if ( didLoadResultsRun ) {
		return;
	} //end if
	didLoadResultsRun = true;

	results = undefined;
	boundingBoxes = {
		desktop: undefined,
		tablet: undefined,
		smartphone: undefined,
	};
	const { dispatch } = options;
	const { setPageAttribute } = dispatch( NAB_DATA );
	setPageAttribute( 'heatmap/dataStatus', { mode: 'loading' } );

	try {
		await loadResultsInBackground( experiment, alternative, options );
	} catch ( e ) {
		setPageAttribute( 'heatmap/dataStatus', {
			mode: 'error',
			rationale: 'promise-failed',
		} );
	} //end try
} //end loadResults()

export function getClicks(): HeatmapResults[ 'clicks' ] {
	if ( ! results ) {
		return [];
	} //end if

	if ( ! boundingBoxes.desktop ) {
		return [];
	} //end if

	return results.clicks;
} //end getClicks()

export function getClicksInResolution(
	resolution: HeatmapResolution
): HeatmapResults[ 'clicks' ] {
	if ( ! results ) {
		return [];
	} //end if

	if ( ! boundingBoxes[ resolution ] || ! results.clicks ) {
		return [];
	} //end if

	const { minWidth, maxWidth } = getMinMaxWidthInResolution( resolution );
	return results.clicks.filter(
		( { realCoordinates: rc, windowWidthInPx: w } ) =>
			minWidth <= w && w <= maxWidth && !! rc?.[ resolution ]
	);
} //end getClicksInResolution()

export function getScrolls(): HeatmapResults[ 'scrolls' ] {
	if ( ! results ) {
		return [];
	} //end if

	return results.scrolls;
} //end getScrolls()

export function recomputeClickCoordinates(): void {
	const wpdata = getWordPressData();
	if ( ! wpdata ) {
		return;
	} //end if

	const { getPageAttribute } = wpdata.select( NAB_DATA );
	const resolution =
		getPageAttribute( 'heatmap/activeResolution' ) ?? 'desktop';
	const mode = getPageAttribute( 'heatmap/activeMode' ) ?? 'heatmap';

	const { setPageAttribute } = wpdata.dispatch( NAB_DATA );
	setPageAttribute( 'heatmap/isProcessingData', true );
	setTimeout( () => {
		boundingBoxes = {
			...boundingBoxes,
			[ resolution ]: undefined,
		};
		const start = new Date().getTime();
		mutateClickCoordinates( resolution, results?.clicks ?? [] );
		const end = new Date().getTime();
		const ellapsedMillis = end - start;

		const atLeastOneSec = Math.max( 0, 1000 - ellapsedMillis );
		setTimeout( () => {
			setPageAttribute( 'heatmap/isProcessingData', false );
			setPageAttribute( 'heatmap/postMessageProxy', {
				plugin: 'nelio-ab-testing',
				type: 'render-results',
				mode,
			} );
		}, 100 + atLeastOneSec );
	}, 100 );
} //end recomputeClickCoordinates()

// =======
// HELPERS
// =======

async function loadResultsInBackground(
	experiment: Experiment,
	alternative: AlternativeIndex,
	options: ResultLoadingOptions
): Promise< void > {
	const { dispatch } = options;
	const { setPageAttribute } = dispatch( NAB_DATA );

	const baseUrl = `${ options.apiUrl }/site/${ options.siteId }/experiment/${ experiment.id }`;
	const args = {
		alternative,
		start: experiment.startDate,
		end:
			experiment.status === 'finished'
				? experiment?.endDate || undefined
				: undefined,
		tz: options.timezone,
	};
	const clicksUrl = addQueryArgs( `${ baseUrl }/clicks`, args );
	const scrollsUrl = addQueryArgs( `${ baseUrl }/scrolls`, args );

	const now = new Date().toISOString();
	totalHoursRunning = getHours( args.start, args.end ?? now );

	const [ clicks, scrolls ] = await Promise.all( [
		loadFirstResults< Click >( options, clicksUrl ),
		loadFirstResults< Scroll >( options, scrollsUrl ),
	] );

	results = { clicks: clicks.data, scrolls: scrolls.data };

	onPageReady( () => {
		mutateClickCoordinates( 'desktop', results?.clicks ?? [] );
		setPageAttribute( 'heatmap/dataStatus', { mode: 'still-loading' } );
		updateStats( options );
		refreshHeatmap();
	} );

	await Promise.all( [
		clicks.page &&
			loadPaginatedResults(
				'clicks',
				options,
				addQueryArgs( clicksUrl, { page: clicks.page } )
			),
		scrolls.page &&
			loadPaginatedResults(
				'scrolls',
				options,
				addQueryArgs( scrollsUrl, { page: scrolls.page } )
			),
	] );

	onPageReady( () => {
		mutateClickCoordinates( 'desktop', results?.clicks ?? [] );
		setPageAttribute( 'heatmap/dataStatus', { mode: 'ready' } );
		updateStats( options );
		refreshHeatmap();
	} );
} //end loadResultsInBackground()

async function loadFirstResults< T extends Click | Scroll >(
	options: ResultLoadingOptions,
	url: string
): Promise< AwsPaginatedResults< T > > {
	const response = await fetch( url, {
		method: 'GET',
		credentials: 'omit',
		mode: 'cors',
		headers: {
			'content-type': 'application/json',
			Authorization: `Bearer ${ options.awsAuthToken }`,
		},
	} );
	const { data: rawData, page } =
		( await response.json() ) as AwsPaginatedResults< T >;

	const data = url.includes( '/clicks' )
		? beautifyClicks( rawData as ReadonlyArray< Click > )
		: rawData;

	if ( url.includes( '/clicks' ) ) {
		mutateClickCoordinates( 'desktop', data as ReadonlyArray< Click > );
	} //end if

	return { data: data as ReadonlyArray< T >, page };
} //end loadFirstResults()

function loadPaginatedResults(
	what: 'clicks' | 'scrolls',
	options: ResultLoadingOptions,
	url: string
): Promise< void > {
	return new Promise( ( resolve ) =>
		loadPage( 1, what, url, options, resolve )
	);
} //end loadPaginatedResults()

function loadPage(
	invocation: number,
	what: 'clicks' | 'scrolls',
	url: string,
	options: ResultLoadingOptions,
	resolve: () => void
): void {
	fetch( url, {
		method: 'GET',
		credentials: 'omit',
		mode: 'cors',
		headers: {
			'content-type': 'application/json',
			Authorization: `Bearer ${ options.awsAuthToken }`,
		},
	} )
		.then< AwsPaginatedResults< HeatmapResults[ typeof what ][ number ] > >(
			( response ) => response.json()
		)
		.then(
			( { data, page } ) => {
				if ( what === 'clicks' ) {
					data = beautifyClicks( data as ReadonlyArray< Click > );
				} //end if

				results = results ?? { clicks: [], scrolls: [] };
				results = {
					...results,
					[ what ]: [ ...results[ what ], ...data ],
				};

				if (
					what === 'clicks' &&
					invocation % PAGES_TO_REFRESH === 0 &&
					isPageReady()
				) {
					mutateClickCoordinates( 'desktop', results[ what ] );
					setTimeout( () => {
						updateStats( options );
						refreshHeatmap();
					}, 0 );
				} //end if

				const newUrl = addQueryArgs( url, { page } );
				if (
					shouldResultLoadingBeStopped ||
					! page ||
					url === newUrl
				) {
					return resolve();
				} //end if

				loadPage( ++invocation, what, newUrl, options, resolve );
			},
			() => resolve()
		);
} // end loadPage()

function beautifyClicks(
	clicks: ReadonlyArray< Click >
): ReadonlyArray< Click > {
	const UNKNOWN = w.parent.nabI18n.unknown;
	return clicks.map( ( click ) => ( {
		...click,
		browser: click.browser || UNKNOWN,
		country: click.country || UNKNOWN,
		dayOfWeek: getDayOfWeekCategory( click.dayOfWeek ),
		device: click.device || UNKNOWN,
		hourOfDay: getHourOfDayCategory( click.hourOfDay ),
		intensity: click.intensity || 1,
		os: click.os || UNKNOWN,
		timeToClick: getTimeToClickCategory( click.timeToClick ),
		windowWidth: getWindowWidthCategory( click.windowWidth ),
		windowWidthInPx:
			'number' === typeof click.windowWidth
				? click.windowWidth
				: Number.parseInt( click.windowWidth ) || 0,
		isVisibleInResolution: {},
		realCoordinates: {},
	} ) );
} //end beautifyClicks()

function updateStats( wpdata: WordPressData ) {
	updateClickCount( wpdata );
	updateProgressEstimate( wpdata );
} //end updateStats()

function updateClickCount( { dispatch }: WordPressData ) {
	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/clickCount',
		results?.clicks.length || 0
	);
} //end updateClickCount()

function updateProgressEstimate( { select, dispatch }: WordPressData ) {
	if ( totalHoursRunning < MIN_HOURS_TO_ESTIMATE_PROGRESS ) {
		return;
	} //end if

	const firstClick = first( results?.clicks );
	const lastClick = last( results?.clicks );
	if ( ! firstClick?.timestamp || ! lastClick?.timestamp ) {
		return;
	} //end if

	const clickHours = getHours( firstClick.timestamp, lastClick.timestamp );
	const actualProgress = ( 100 * clickHours ) / totalHoursRunning;
	const progress = Math.max( 1, Math.floor( actualProgress ) );

	const prevProgress =
		select( NAB_DATA ).getPageAttribute( 'heatmap/progress' ) ?? 0;
	if ( progress > prevProgress ) {
		dispatch( NAB_DATA ).setPageAttribute( 'heatmap/progress', progress );
	} //end if
} //end updateProgressEstimate()

function mutateClickCoordinates(
	resolution: HeatmapResolution,
	clicks: ReadonlyArray< Click >
): void {
	if ( ! isPageReady ) {
		return;
	} //end if

	const boxes = computeBoundingBoxes( resolution, clicks );
	clicks.forEach( ( click ) => {
		const { x, y } = click;
		const { top, left, width, height } =
			boxes[ click.xpath || click.cssPath ] || NO_BB;

		const realX = left + x * width;
		const realY = top + y * height;

		click.realCoordinates = click.realCoordinates || {};
		click.realCoordinates[ resolution ] = {
			...( click.realCoordinates[ resolution ] || {} ),
			x: realX,
			y: realY,
		};

		click.isVisibleInResolution = click.isVisibleInResolution || {};
		click.isVisibleInResolution[ resolution ] = width > 0 && height > 0;
	} );

	boundingBoxes[ resolution ] = boxes;
} //end mutateClickCoordinates()

const computeBoundingBoxes = (
	resolution: HeatmapResolution,
	clicks: ReadonlyArray< Click >
): Record< string, BoundingBox > =>
	mapValues(
		keyBy(
			clicks.map( ( c ) => ( {
				...c,
				_selector: c.xpath || c.cssPath,
			} ) ),
			'_selector'
		),
		( click ): BoundingBox => {
			const selector = click.xpath || click.cssPath;
			if ( ! selector ) {
				return NO_BB;
			} //end if

			const existingBoundingBox =
				boundingBoxes[ resolution ]?.[ selector ];
			if ( existingBoundingBox ) {
				return existingBoundingBox;
			} //end if

			const el = getAllSelectors( selector ).reduce( ( r, s ) => {
				try {
					return r || click.xpath
						? getElementByXPath( s )
						: document.querySelector( s );
				} catch ( e ) {
					return r;
				}
			}, null as Node | null );

			if ( ! isHTMLElement( el ) ) {
				return NO_BB;
			} //end if

			const bodyBox = document.body.getBoundingClientRect();
			const elBox = el.getBoundingClientRect();
			return {
				top: elBox.top - bodyBox.top,
				left: elBox.left - bodyBox.left,
				width: elBox.width,
				height: elBox.height,
			};
		}
	);

function getAllSelectors( selector: string ): ReadonlyArray< string > {
	const wpdata = getWordPressData();
	if ( ! wpdata ) {
		return [ selector ];
	} //end if

	const { getExperiment, getPageAttribute } = wpdata.select( NAB_DATA );
	const experimentId = getPageAttribute( 'editor/activeExperiment' );
	const experiment = getExperiment( experimentId ?? 0 );
	if ( ! experiment ) {
		return [ selector ];
	} //end if

	if ( experiment.type === 'nab/heatmap' ) {
		return [ selector ];
	} //end if

	const altIdx = getPageAttribute( 'heatmap/activeAlternative' );
	if ( altIdx === 0 ) {
		return [ selector ];
	} //end if

	const control = experiment.alternatives[ 0 ];
	const alternative =
		undefined !== altIdx ? experiment.alternatives[ altIdx ] : undefined;

	const controlPost = control?.attributes.postId;
	const alternativePost = alternative?.attributes.postId;
	if ( ! isPostId( controlPost ) || ! isPostId( alternativePost ) ) {
		return [ selector ];
	} //end if

	return [
		selector,
		selector.replace(
			new RegExp( `([\\b_])?${ controlPost }([\\b_])?`, 'g' ),
			`$1${ alternativePost }$2`
		),
		selector.replace(
			new RegExp( `([\\b_])?${ alternativePost }([\\b_])?`, 'g' ),
			`$1${ controlPost }$2`
		),
	];
} //end getAlternativeSelector()

const isPostId = ( id: unknown ): id is number =>
	'number' === typeof id && 0 < id;

function getHourOfDayCategory( hourOfDay: string | number ): string {
	if ( 'string' === typeof hourOfDay ) {
		hourOfDay = Number.parseInt( hourOfDay );
		if ( isNaN( hourOfDay ) ) {
			hourOfDay = 0;
		} //end if
	} //end if
	return `${ Math.floor( hourOfDay / 2 ) }`;
} //end getHourOfDayCategory()

function getDayOfWeekCategory( dayOfWeek: string | number ): string {
	return `${ dayOfWeek }`;
} //end getDayOfWeekCategory()

function getTimeToClickCategory( timeToClick: string | number ): string {
	if ( 'string' === typeof timeToClick ) {
		timeToClick = Number.parseInt( timeToClick );
	} //end if

	const cuts = [ 2, 3, 4, 5, 10, 15, 20, 25, 30, 60, 120, 180, 300 ] as const;
	for ( let i = 0; i < cuts.length; ++i ) {
		const cut = cuts[ i ];
		if ( cut && timeToClick <= cut ) {
			return `${ i }`;
		} //end if
	} //end for

	const lastCut = cuts[ cuts.length - 1 ] || 300;
	return `${ lastCut }`;
} //end getTimeToClickCategory()

function getWindowWidthCategory( windowWidth: string | number ): string {
	if ( 'string' === typeof windowWidth ) {
		windowWidth = Number.parseInt( windowWidth );
	} //end if

	const cuts = [ 300, 600, 900, 1200, 1500, 1800, 2100 ];
	for ( let i = 0; i < cuts.length; ++i ) {
		const cut = cuts[ i ];
		if ( cut && windowWidth < cut ) {
			return `${ i }`;
		} //end if
	} //end for

	const lastCut = cuts[ cuts.length - 1 ] || 2100;
	return `${ lastCut }`;
} //end getWindowWidthCategory()

function isHTMLElement( n: Node | null ): n is HTMLElement {
	return !! n && Node.ELEMENT_NODE === n.nodeType;
} //end isHTMLElement()

// =======
// GLOBALS
// =======

const w = window as unknown as Window & {
	parent: {
		nabI18n: HeatmapLabels;
	};
};
