/**
 * External dependencies
 */
import debounce from 'lodash/debounce';
import keyBy from 'lodash/keyBy';
import orderBy from 'lodash/orderBy';
import type {
	ConfettiOption,
	ConfettiType,
	Dict,
	HeatmapFrameAction,
	HeatmapLabels,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { getClicks, recomputeClickCoordinates } from './internal/store';
import { getConfettiColor, getDefaultColor } from './internal/confetti-colors';
import {
	areResults,
	createOverlayElement,
	getWordPressData,
	maximizeElement,
} from './internal/utils';

import type { Click } from './internal/types';

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';
const STATS_REFRESH_DELAY = 100;

const confetti: {
	canvas?: HTMLCanvasElement;
	overlay?: HTMLElement;
	stats?: HTMLElement;
	statsTracker?: HTMLElement;
} = {};

let actualFilterOptions: Partial<
	Record< ConfettiType, Dict< ConfettiOption > >
>;

let visibleClicks: ReadonlyArray< Click > = [];

export function reactToParentMessages( action: HeatmapFrameAction ): true {
	if ( 'confetti' !== action.mode ) {
		removeConfettiFromBody();
		return true;
	} //end if

	if ( ! areResults( 'ready' ) ) {
		return true;
	} //end if

	switch ( action.type ) {
		case 'recompute-click-coordinates':
			recomputeClickCoordinates();
			return true;

		case 'render-results':
			renderResults();
			return true;

		case 'update-opacity':
			setOverlayOpacity( action.value );
			return true;
	} //end if
} //end reactToParentMessages()

// =======
// HELPERS
// =======

function renderResults() {
	const wpdata = getWordPressData();
	if ( ! wpdata ) {
		return;
	} //end if

	if ( ! confetti.canvas ) {
		initConfetti();
	} //end if

	if ( ! actualFilterOptions ) {
		initFilterOptions();
		wpdata
			.dispatch( NAB_DATA )
			.setPageAttribute(
				'heatmap/confettiFilterOptions',
				actualFilterOptions
			);
	} //end if

	const { getPageAttribute: get } = wpdata.select( NAB_DATA );
	const activeFilter = get( 'heatmap/activeFilter' ) ?? 'timeToClick';
	const disabledFilterOptions = get( 'heatmap/disabledFilterOptions' ) ?? [];
	const opacity = get( 'heatmap/opacity' ) ?? 80;
	updateVisibleClicks( activeFilter, disabledFilterOptions );
	setOverlayOpacity( opacity );
	appendConfettiInBody();
	renderConfetti( activeFilter );
} //end renderResults()

function setOverlayOpacity( opacity: number ) {
	if ( ! confetti.overlay || undefined === opacity ) {
		return;
	} //end if
	confetti.overlay.style.opacity = `${ opacity / 100 }`;
} //end setOverlayOpacity()

function removeConfettiFromBody() {
	if ( document.body === confetti.canvas?.parentNode ) {
		document.body.removeChild( confetti.canvas );
	} //end if

	if ( document.body === confetti.overlay?.parentNode ) {
		document.body.removeChild( confetti.overlay );
	} //end if

	if ( document.body === confetti.stats?.parentNode ) {
		document.body.removeChild( confetti.stats );
	} //end if

	if ( document.body === confetti.statsTracker?.parentNode ) {
		document.body.removeChild( confetti.statsTracker );
	} //end if
} //end removeConfettiFromBody()

function appendConfettiInBody() {
	if (
		! confetti.overlay ||
		! confetti.canvas ||
		! confetti.stats ||
		! confetti.statsTracker
	) {
		return;
	} //end if

	maximizeElement( confetti.overlay );
	maximizeElement( confetti.canvas );

	if ( document.body === confetti.canvas.parentNode ) {
		return;
	} //end if

	document.body.appendChild( confetti.overlay );
	document.body.appendChild( confetti.canvas );
	document.body.appendChild( confetti.stats );
	document.body.appendChild( confetti.statsTracker );
} //end appendConfettiInBody()

function initConfetti() {
	confetti.overlay = createOverlayElement( 'div' );
	confetti.overlay.style.background = '#000';
	confetti.canvas = createOverlayElement< HTMLCanvasElement >( 'canvas' );
	// Stats.

	confetti.stats = createOverlayElement( 'div' );
	confetti.stats.style.height = 'auto';
	confetti.stats.style.borderBottom = '2px dashed #262b2d';
	confetti.stats.style.opacity = '0';
	confetti.stats.style.transition = 'opacity 200ms ease-in-out';

	const scrollStatsContainer = createElement( 'span', {
		style: {
			fontSize: '13px',
			left: '1em',
			borderRadius: '4px',
			fontWeight: '600',
			fontFamily:
				'-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif',
			background: '#262b2d',
			pointerEvents: 'none',
			color: '#eee',
			display: 'inline-block',
			position: 'absolute',
			transform: 'translate(0, -50%)',
			whiteSpace: 'nowrap',
		},
	} );

	const statsPopularity = createElement( 'span', {
		className: 'nab-data-block',
		style: { padding: '0 1em' },
	} );
	const statsPopularityLabel = document.createTextNode(
		w.parent.nabI18n.clicksAbove + ' '
	);
	const statsPopularityValue = createElement( 'output', {
		id: 'nab-clicks-above',
	} );
	statsPopularityValue.style.color = 'inherit';
	statsPopularityValue.style.display = 'inline';
	statsPopularityValue.style.fontSize = 'inherit';
	statsPopularityValue.style.lineHeight = 'inherit';
	statsPopularityValue.style.margin = '0';
	statsPopularityValue.style.padding = '0';

	scrollStatsContainer.appendChild( statsPopularity );
	statsPopularity.appendChild( statsPopularityLabel );
	statsPopularity.appendChild( statsPopularityValue );

	// Stats tracker.
	confetti.statsTracker = createOverlayElement( 'div' );

	// Event listeners.
	confetti.stats.appendChild( scrollStatsContainer );

	confetti.statsTracker.addEventListener( 'mouseenter', () => {
		if ( confetti.stats ) {
			confetti.stats.style.opacity = '1';
		} //end if
	} );

	confetti.statsTracker.addEventListener( 'mouseleave', () => {
		if ( confetti.stats ) {
			confetti.stats.style.opacity = '0';
		} //end if
	} );

	// Update positon.
	confetti.statsTracker.addEventListener( 'mousemove', ( event ) => {
		if ( ! confetti.stats ) {
			return;
		} //end if
		confetti.stats.style.top = `${ Math.max( event.pageY, 0 ) }px`;
	} );

	// Update count.
	confetti.statsTracker.addEventListener(
		'mousemove',
		debounce( ( event: MouseEvent ) => {
			const clicksEl = document.getElementById( 'nab-clicks-above' );
			if ( ! clicksEl ) {
				return;
			} //end if
			const clicks = visibleClicks.filter(
				( click ) =>
					( click.realCoordinates.desktop?.y ||
						Number.POSITIVE_INFINITY ) <= event.pageY
			);
			clicksEl.textContent = `${ clicks.length }`;
		}, STATS_REFRESH_DELAY )
	);
} //end initConfetti()

function initFilterOptions() {
	const clicks = getClicks();
	actualFilterOptions = getFilterOptions( clicks );
} //end initFilterOptions()

function shouldClickBeRendered(
	click: Click,
	activeFilter: ConfettiType,
	disabledFilterOptions: ReadonlyArray< string >
) {
	let filterValue = click[ activeFilter ];
	if (
		! Object.keys( actualFilterOptions[ activeFilter ] || {} ).includes(
			`${ filterValue }`
		)
	) {
		filterValue = 'other';
	} //end if

	return ! disabledFilterOptions.includes( `${ filterValue }` );
} //end shouldClickBeRendered()

export function getFilterOptions(
	clicks: ReadonlyArray< Click >
): Record< ConfettiType, Dict< ConfettiOption > > {
	const resultsSummary: Record< ConfettiType, Dict< number > > = {
		country: {},
		device: {},
		os: {},
		browser: {},
		dayOfWeek: {},
		hourOfDay: {},
		timeToClick: {},
		windowWidth: {},
	};

	clicks.forEach( ( click ) => {
		resultsSummary.country[ click.country ] =
			( resultsSummary.country[ click.country ] || 0 ) + click.intensity;
		resultsSummary.device[ click.device ] =
			( resultsSummary.device[ click.device ] || 0 ) + click.intensity;
		resultsSummary.os[ click.os ] =
			( resultsSummary.os[ click.os ] || 0 ) + click.intensity;
		resultsSummary.browser[ click.browser ] =
			( resultsSummary.browser[ click.browser ] || 0 ) + click.intensity;
		resultsSummary.dayOfWeek[ click.dayOfWeek ] =
			( resultsSummary.dayOfWeek[ click.dayOfWeek ] || 0 ) +
			click.intensity;
		resultsSummary.hourOfDay[ click.hourOfDay ] =
			( resultsSummary.hourOfDay[ click.hourOfDay ] || 0 ) +
			click.intensity;
		resultsSummary.timeToClick[ click.timeToClick ] =
			( resultsSummary.timeToClick[ click.timeToClick ] || 0 ) +
			click.intensity;
		resultsSummary.windowWidth[ click.windowWidth ] =
			( resultsSummary.windowWidth[ click.windowWidth ] || 0 ) +
			click.intensity;
	} );

	const keys = Object.keys( resultsSummary ) as ReadonlyArray< ConfettiType >;
	return keys.reduce(
		( memo, key ) => ( {
			...memo,
			[ key ]: processFilter( key, resultsSummary[ key ] ),
		} ),
		{} as Record< ConfettiType, Dict< ConfettiOption > >
	);
} //end getFilterOptions()

function processFilter(
	filter: ConfettiType,
	filterOptions: Dict< number >
): Dict< ConfettiOption > {
	switch ( filter ) {
		case 'country':
			return getFilterOptionsForDiscreteFilter( filter, filterOptions );

		case 'device':
			return getFilterOptionsForDiscreteFilter( filter, filterOptions );

		case 'os':
			return sortOsOptions(
				getFilterOptionsForDiscreteFilter( filter, filterOptions )
			);

		case 'browser': {
			return sortBrowserOptions(
				getFilterOptionsForDiscreteFilter( filter, filterOptions )
			);
		}

		case 'dayOfWeek':
		case 'hourOfDay':
		case 'timeToClick':
		case 'windowWidth':
			return getFilterOptionsForContinuousFilter( filter, filterOptions );
	} //end switch
} //end processFilterOption()

function getFilterOptionsForDiscreteFilter(
	filter: ConfettiType,
	filterOptions: Dict< number >
) {
	const orderedOptions = orderBy(
		Object.keys( filterOptions ).map( ( key ) => ( {
			key,
			value: filterOptions[ key ],
		} ) ),
		'value',
		'desc'
	);

	let relevantOptions = orderedOptions.splice( 0, 14 );
	relevantOptions = orderBy( relevantOptions, 'key' );

	if ( orderedOptions.length ) {
		relevantOptions.push( {
			key: 'other',
			value: orderedOptions.reduce(
				( total, { value } ) => total + ( value ?? 0 ),
				0
			),
		} );
	} //end if

	let defaultColor = -1;
	return keyBy(
		relevantOptions.map(
			( { key, value } ): ConfettiOption => ( {
				key,
				label: getLabelOfDiscreteFilterOption( filter, key ),
				value: `${ value ?? '' }`,
				color:
					getConfettiColor( filter, key ) ||
					getDefaultColor( ++defaultColor ),
			} )
		),
		'key'
	);
} //end getFilterOptionsForDiscreteFilter()

function getLabelOfDiscreteFilterOption(
	filter: ConfettiType,
	filterOptionKey: string
) {
	if ( 'other' === filterOptionKey ) {
		return w.parent.nabI18n.other;
	} //end if

	switch ( filter ) {
		case 'device':
			return (
				( filterOptionKey[ 0 ]?.toUpperCase() ?? '' ) +
				filterOptionKey.slice( 1 )
			);

		case 'country':
		case 'os':
		case 'browser':
		default:
			return filterOptionKey;
	} //end switch
} //end getLabelOfDiscreteFilterOption()

function getFilterOptionsForContinuousFilter(
	filter: 'dayOfWeek' | 'hourOfDay' | 'timeToClick' | 'windowWidth',
	filterOptions: Dict< number >
) {
	const filterCategories = {
		dayOfWeek: [
			{ label: w.parent.nabI18n.dayOfWeek.sunday },
			{ label: w.parent.nabI18n.dayOfWeek.monday },
			{ label: w.parent.nabI18n.dayOfWeek.tuesday },
			{ label: w.parent.nabI18n.dayOfWeek.wednesday },
			{ label: w.parent.nabI18n.dayOfWeek.thursday },
			{ label: w.parent.nabI18n.dayOfWeek.friday },
			{ label: w.parent.nabI18n.dayOfWeek.saturday },
		],
		hourOfDay: [
			{ label: w.parent.nabI18n.hourOfDay[ '12a - 2a' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '2a - 4a' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '4a - 6a' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '6a - 8a' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '8a - 10a' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '10a - 12p' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '12p - 2p' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '2p - 4p' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '4p - 6p' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '6p - 8p' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '8p - 10p' ] },
			{ label: w.parent.nabI18n.hourOfDay[ '10p - 12a' ] },
		],
		timeToClick: [
			{ label: w.parent.nabI18n.timeToClick[ '0 - 2s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '2 - 3s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '3 - 4s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '4 - 5s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '5 - 10s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '10 - 15s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '15 - 20s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '20 - 25s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '25 - 30s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '30 - 60s' ] },
			{ label: w.parent.nabI18n.timeToClick[ '1 - 2min' ] },
			{ label: w.parent.nabI18n.timeToClick[ '2 - 3min' ] },
			{ label: w.parent.nabI18n.timeToClick[ '3 - 5min' ] },
			{ label: w.parent.nabI18n.timeToClick[ '+5min' ] },
		],
		windowWidth: [
			{ label: w.parent.nabI18n.windowWidth[ '< 300 px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '300 - 600px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '600 - 900px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '900 - 1200px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '1200 - 1500px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '1500 - 1800px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '1800 - 2100px' ] },
			{ label: w.parent.nabI18n.windowWidth[ '+2100px' ] },
		],
	};

	const categoriesOfFilter = filterCategories[ filter ].map(
		( { label }, index ) => ( {
			key: `${ index }`,
			label,
			value: filterOptions[ index ] || 0,
			color: '',
		} )
	);

	let defaultColor = -1;
	return keyBy(
		categoriesOfFilter.map(
			( category ): ConfettiOption => ( {
				...category,
				color:
					getConfettiColor( filter, category.key ) ||
					getDefaultColor( ++defaultColor ),
			} )
		),
		'key'
	);
} //end getFilterOptionsForContinuousFilter()

function sortOsOptions( options: Dict< ConfettiOption > ) {
	const windows: Dict< string > = {
		'Windows 10': 'Windows 01',
		'Windows Phone': 'Windows 02',
		'Windows 7': 'Windows 03',
		'Windows Vista': 'Windows 04',
		'Windows XP': 'Windows 05',
		'Windows 98': 'Windows 06',
		'Windows 95': 'Windows 07',
		Windows: 'Windows 08',
	};

	let aux = Object.values( options ).map( ( { key, ...option } ) => ( {
		key,
		...option,
		sorting: windows[ key ] ? windows[ key ] : key,
	} ) );
	aux = orderBy( aux, 'sorting' );

	const sortedOptions = aux.map( ( opt ) => ( {
		key: opt.key,
		color: opt.color,
		value: opt.value,
		label: opt.label,
	} ) );

	return keyBy( sortedOptions, 'key' );
} //end sortOsOptions()

function sortBrowserOptions( options: Dict< ConfettiOption > ) {
	const sortedOptions = orderBy( Object.values( options ), 'key' );
	return keyBy( sortedOptions, 'key' );
} //end sortBrowserOptions()

function updateVisibleClicks(
	activeFilter: ConfettiType,
	disabledFilterOptions: ReadonlyArray< string >
) {
	visibleClicks = getClicks().filter( ( c ) =>
		shouldClickBeRendered( c, activeFilter, disabledFilterOptions )
	);
} //end updateVisibleClicks()

function renderConfetti( activeFilter: ConfettiType ) {
	if ( ! confetti.canvas ) {
		return;
	} //end if

	const ctx = confetti.canvas.getContext( '2d' );
	if ( ! ctx ) {
		return;
	} //end if

	ctx.clearRect( 0, 0, confetti.canvas.width, confetti.canvas.height );
	ctx.lineWidth = 1;
	ctx.strokeStyle = '#000';

	visibleClicks.forEach( ( c ) => renderClick( ctx, c, activeFilter ) );
} //end renderConfetti()

function renderClick(
	ctx: CanvasRenderingContext2D,
	click: Click,
	activeFilter: ConfettiType
) {
	if ( ! click.realCoordinates || ! click.realCoordinates.desktop ) {
		return;
	} //end if

	if ( ! click.isVisibleInResolution?.desktop ) {
		return;
	} //end if

	const { x, y } = click.realCoordinates.desktop;
	const options = actualFilterOptions[ activeFilter ] || {};

	ctx.fillStyle =
		options[ click[ activeFilter ] ]?.color ||
		getConfettiColor( activeFilter, 'other' );
	ctx.beginPath();
	ctx.arc( x, y, 6, 0, 2 * Math.PI, true );
	ctx.closePath();
	ctx.fill();
	ctx.stroke();
} //end renderClick()

// TODO. This is duplicated from Scrollmap. Refactor?
function createElement< T extends HTMLElement = HTMLElement >(
	type: string,
	options: Dict & { style?: Dict< string > } = {}
): T {
	const el = document.createElement( type );

	const { id, className, style, ...props } = options;
	if ( 'string' === typeof id ) {
		el.id = id;
	} //end if

	if ( 'string' === typeof className ) {
		el.className = className;
	} //end if

	if ( 'object' === typeof style ) {
		Object.keys( style ).forEach( ( k ) => {
			( el as unknown as { style: Dict } ).style[ k ] = style[ k ];
		} );
	} //end if

	Object.keys( props ).forEach( ( p ) => {
		( el as unknown as Dict )[ p ] = props[ p ];
	} );

	return el as T;
} //end createElement()

// =======
// GLOBALS
// =======

const w = window as unknown as Window & {
	parent: {
		nabI18n: HeatmapLabels;
	};
};
