/**
 * External dependencies
 */
import type { HeatmapFrameAction, HeatmapResolution } from '@nab/types';

/**
 * Internal dependencies
 */
import { domReady } from '../../../public/utils/helpers';
import {
	getClicksInResolution,
	recomputeClickCoordinates,
	stopResultLoading,
} from './internal/store';
import {
	areResults,
	createOverlayElement,
	getDocumentHeight,
	getWordPressData,
	maximizeElement,
} from './internal/utils';
import { WebGLHeatmap, createWebGLHeatmap } from './lib/webgl-heatmap';
import type { Click } from './internal/types';

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';

const GL_CANVAS_SCALE = 0.25;
const HEATMAP_POINT_SIZE = 100;
const MAX_SIZE_MULTIPLIER = 2;
const MIN_INTENSITY = 0.4;
const MAX_INTENSITY = 2;
const INTENSITY_RANGE = MAX_INTENSITY - MIN_INTENSITY;

type Heatmap = {
	canvas: HTMLCanvasElement;
	instance: WebGLHeatmap;
	overlay: HTMLElement;
};

type ClicksPerSquare = {
	readonly columns: number;
	readonly value: ReadonlyArray< number >;
	readonly height: number;
	readonly max: number;
	readonly rows: number;
	readonly width: number;
};

const heatmap: Heatmap = initWebGLHeatmap();
let clicksPerSquare: Partial< Record< HeatmapResolution, ClicksPerSquare > > =
	{};

// Add overlay on startup.
domReady( appendHeatmapInBody );

export function reactToParentMessages( action: HeatmapFrameAction ): true {
	if ( 'heatmap' !== action.mode ) {
		removeHeatmapFromBody();
		return true;
	} //end if

	if ( ! areResults( [ 'still-loading', 'ready' ] ) ) {
		return true;
	} //end if

	switch ( action.type ) {
		case 'recompute-click-coordinates':
			clicksPerSquare = {};
			recomputeClickCoordinates();
			return true;

		case 'stop-result-loading':
			stopResultLoading();
			return true;

		case 'render-results':
			refreshHeatmap();
			return true;

		case 'update-intensity':
			updateIntensity();
			return true;

		case 'update-opacity':
			setOverlayOpacity( action.value );
			return true;
	} //end if
} //end reactToParentMessages()

export function refreshHeatmap(): void {
	const wpdata = getWordPressData();
	if ( ! wpdata ) {
		return;
	} //end if

	resetHeatmap();
	renderHeatmap();
} //end refreshHeatmap();

// =======
// HELPERS
// =======

const updateIntensity = renderHeatmap;

function resetHeatmap() {
	const { canvas, instance } = initWebGLHeatmap();
	clicksPerSquare = {};
	removeHeatmapFromBody();
	heatmap.canvas = canvas;
	heatmap.instance = instance;
	appendHeatmapInBody();
} //end resetHeatmap()

function setOverlayOpacity( opacity: number ) {
	if ( ! heatmap.overlay || undefined === opacity ) {
		return;
	} //end if
	heatmap.overlay.style.opacity = `${ opacity / 100 }`;
} //end setOverlayOpacity()

function removeHeatmapFromBody() {
	if ( document.body === heatmap.canvas?.parentNode ) {
		document.body.removeChild( heatmap.canvas );
	} //end if

	if ( document.body === heatmap.overlay?.parentNode ) {
		document.body.removeChild( heatmap.overlay );
	} //end if
} //end removeHeatmapFromBody()

function appendHeatmapInBody() {
	if ( ! heatmap.canvas || ! heatmap.overlay ) {
		return;
	} //end if

	if ( document.body === heatmap.canvas.parentNode ) {
		maximizeHeatmap();
		return;
	} //end if

	document.body.appendChild( heatmap.overlay );
	document.body.appendChild( heatmap.canvas );
	maximizeHeatmap();
} //end appendHeatmapInBody()

function maximizeHeatmap() {
	if ( ! heatmap.canvas || ! heatmap.instance || ! heatmap.overlay ) {
		return;
	} //end if

	maximizeElement( heatmap.overlay );

	const width = document.body.clientWidth * GL_CANVAS_SCALE;
	const height = getDocumentHeight() * GL_CANVAS_SCALE;
	heatmap.canvas.style.width = `${ width }px`;
	heatmap.canvas.style.height = `${ height }px`;
	heatmap.instance.adjustSize( width, height );
} //end maximizeHeatmap()

function initWebGLHeatmap(): Heatmap {
	const overlay = createOverlayElement( 'div' );
	overlay.style.background = '#000';
	overlay.style.opacity = '0.8';

	const canvas = createOverlayElement< HTMLCanvasElement >( 'canvas' );
	canvas.style.mixBlendMode = 'screen';
	canvas.style.transform = `scale(${ ( 1 / GL_CANVAS_SCALE ).toFixed( 3 ) })`;
	canvas.style.transformOrigin = '0 0';

	const instance = createWebGLHeatmap( {
		canvas,
	} );
	return { overlay, canvas, instance };
} //end initWebGLHeatmap()

function renderHeatmap() {
	const wpdata = getWordPressData();
	if ( ! wpdata ) {
		return;
	} //end if

	if ( ! heatmap.instance ) {
		return;
	} //end if

	const { getPageAttribute: get } = wpdata.select( NAB_DATA );
	const activeResolution = get( 'heatmap/activeResolution' ) ?? 'desktop';
	const heatmapIntensity = get( 'heatmap/intensity' ) ?? 80;
	const opacity = get( 'heatmap/opacity' ) ?? 80;

	removeHeatmapFromBody();
	setOverlayOpacity( opacity );
	appendHeatmapInBody();

	const clicks = getClicksInResolution( activeResolution );
	if ( ! clicksPerSquare[ activeResolution ] ) {
		computeClicksPerSquareInResolution( clicks, activeResolution );
	} //end if

	const cps = clicksPerSquare[ activeResolution ];
	if ( ! cps ) {
		return;
	} //end if

	const maxClicksInSquare = cps.max;
	const intensity = heatmapIntensity / 100;

	heatmap.instance.clear();
	clicks.forEach( ( click ) =>
		renderClick( click, maxClicksInSquare, activeResolution )
	);
	heatmap.instance.update();
	heatmap.instance.multiply( intensity );
	heatmap.instance.update();
	heatmap.instance.display();
} //end renderHeatmap()

function renderClick(
	click: Click,
	maxClicksInSquare: number,
	resolution: HeatmapResolution
) {
	if ( ! heatmap.instance ) {
		return;
	} //end if

	if ( ! click.isVisibleInResolution[ resolution ] ) {
		return;
	} //end if

	const coords = click.realCoordinates[ resolution ];
	if ( ! coords ) {
		return;
	} //end if

	const { x, y } = coords;
	const clicksInSquare = getClicksInSquare( click, resolution );
	const normalizedSquareIntensity =
		( clicksInSquare / maxClicksInSquare ) * INTENSITY_RANGE +
		MIN_INTENSITY;
	const clickIntensity = normalizedSquareIntensity / clicksInSquare;

	const scale = mapScale(
		clicksInSquare,
		0,
		maxClicksInSquare,
		MAX_SIZE_MULTIPLIER,
		1
	);

	heatmap.instance.addPoint(
		x * GL_CANVAS_SCALE,
		y * GL_CANVAS_SCALE,
		scale * HEATMAP_POINT_SIZE * GL_CANVAS_SCALE,
		clickIntensity
	);
} //end renderClick()

function mapScale(
	value: number,
	inputStart: number,
	inputEnd: number,
	outputStart: number,
	outputEnd: number
) {
	const slope = ( outputEnd - outputStart ) / ( inputEnd - inputStart );
	return outputStart + slope * ( value - inputStart );
} //end mapScale()

function getClicksInSquare(
	click: Click,
	resolution: HeatmapResolution
): number {
	const cps = clicksPerSquare[ resolution ];
	const coords = click.realCoordinates[ resolution ];
	if ( ! cps || ! coords ) {
		return 1;
	} //end if

	const { x, y } = coords;
	const { columns, value } = cps;
	const col = Math.floor( x / HEATMAP_POINT_SIZE );
	const row = Math.floor( y / HEATMAP_POINT_SIZE );

	const index = row * columns + col;
	if ( index < 0 && value.length <= index ) {
		return 1;
	} //end if

	return cps.value[ index ] ?? 0;
} //end getClicksInSquare()

function computeClicksPerSquareInResolution(
	clicks: ReadonlyArray< Click >,
	resolution: HeatmapResolution
) {
	if ( clicksPerSquare[ resolution ] ) {
		return;
	} //end if

	const width = document.body.clientWidth;
	const height = getDocumentHeight();

	const NUM_OF_COLS = Math.ceil( width / HEATMAP_POINT_SIZE );
	const NUM_OF_ROWS = Math.ceil( height / HEATMAP_POINT_SIZE );

	const data = new Array( NUM_OF_ROWS * NUM_OF_COLS ).fill( 0 );
	clicks.forEach( ( click ) => {
		if ( ! click.isVisibleInResolution[ resolution ] ) {
			return;
		} //end if

		const coords = click.realCoordinates[ resolution ];
		if ( ! coords ) {
			return;
		} //end if

		const { x, y } = coords;
		const col = Math.floor( x / HEATMAP_POINT_SIZE );
		const row = Math.floor( y / HEATMAP_POINT_SIZE );

		const index = row * NUM_OF_COLS + col;
		if ( index < 0 && data.length <= index ) {
			return;
		} //end if
		data[ index ] += click.intensity;
	} );

	clicksPerSquare[ resolution ] = {
		columns: NUM_OF_COLS,
		value: data,
		height,
		max: Math.max(
			1,
			data.reduce( ( max, value ) => Math.max( max, value || 0 ) )
		),
		rows: NUM_OF_ROWS,
		width,
	};
} //end computeClicksPerSquareInResolution()
