/**
 * External dependencies
 */
import type {
	Dims,
	HeatmapDataStatus,
	HeatmapResolution,
	Maybe,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { getDocumentHeight as _getDocumentHeight } from '../../../../public/utils/helpers/get-document-height';
import type { WordPressData } from './types';

// NOTE. No @nab packages in front.
const NAB_DATA = 'nab/data';
const MAX_DESKTOP_HEIGHT = 100_000;
const HOUR_IN_MILLIS = 3_600_000;

const RESOLUTIONS: Record< HeatmapResolution, Dims > = {
	desktop: {
		width: 1600,
		height: 900,
	},
	tablet: {
		width: 1024,
		height: 576,
	},
	smartphone: {
		width: 460,
		height: 818,
	},
};

export const getHours = ( d1: string, d2: string ): number =>
	Math.ceil(
		Math.abs( new Date( d1 ).getTime() - new Date( d2 ).getTime() ) /
			HOUR_IN_MILLIS
	);

export const getDocumentHeight = (): number => {
	const pairs = Array.from(
		document.querySelectorAll( '.nab-overlay-element' )
	)
		.map( ( el ) => [ el.parentElement, el ] )
		.filter(
			( pair ): pair is [ HTMLElement, HTMLElement ] => !! pair[ 0 ]
		);

	pairs.forEach( ( [ p, c ] ) => p.removeChild( c ) );
	const height = Math.min( _getDocumentHeight(), MAX_DESKTOP_HEIGHT );
	pairs.forEach( ( [ p, c ] ) => p.appendChild( c ) );
	return height;
};

export const getWordPressData = (): Maybe< WordPressData > =>
	// eslint-disable-next-line
	( ( window.parent as any )?.wp as any ).data as WordPressData;

export function areResults(
	mode: HeatmapDataStatus[ 'mode' ] | HeatmapDataStatus[ 'mode' ][]
): boolean {
	const select = getWordPressData()?.select;
	if ( ! select ) {
		return false;
	} //end if

	const { getPageAttribute } = select( NAB_DATA );
	const modes = 'string' === typeof mode ? [ mode ] : mode;
	return modes.some(
		( m ) => m === getPageAttribute( 'heatmap/dataStatus' )?.mode
	);
} //end areResults()

export function createOverlayElement< T extends HTMLElement = HTMLElement >(
	type: string
): T {
	const el = document.createElement( type );
	el.className = 'nab-overlay-element';
	el.style.position = 'absolute';
	el.style.left = '0px';
	el.style.top = '0px';
	el.style.zIndex = '999999';
	maximizeElement( el );

	return el as T;
} //end createOverlayElement()

export function maximizeElement( el: HTMLElement ): void {
	const width = document.body.clientWidth;
	const height = getDocumentHeight();

	el.style.width = `${ width }px`;
	el.style.height = `${ height }px`;

	if ( isHTMLCanvas( el ) ) {
		el.width = width;
		el.height = height;
	} //end if
} //end maximizeElement()

export function getMinMaxWidthInResolution(
	activeResolution: HeatmapResolution
): {
	readonly minWidth: number;
	readonly maxWidth: number;
} {
	const expectedWidth = RESOLUTIONS[ activeResolution ].width;
	const cuts = Object.values( RESOLUTIONS ).map( ( { width } ) => width );

	const minWidth = cuts.reduce(
		( memo, width ) =>
			width < expectedWidth ? Math.max( width, memo ) : memo,
		-1
	);

	let maxWidth = cuts.reduce(
		( memo, width ) =>
			expectedWidth < width ? Math.min( width, memo ) : memo,
		Number.POSITIVE_INFINITY
	);
	if ( Number.POSITIVE_INFINITY !== maxWidth ) {
		maxWidth = expectedWidth;
	} //end if

	return { minWidth: minWidth + 1, maxWidth };
} //end getMinMaxWidthInResolution()

const isHTMLCanvas = ( el: HTMLElement ): el is HTMLCanvasElement =>
	'canvas' === el.nodeName.toLowerCase();
